<template>
  <div class="newTingpage">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <van-cell
        @click="clickhandle(item.id)"
        v-for="(item, index) in newtinglist"
        :key="index"
      >
        <van-image
          width="70rem"
          height="70rem"
          lazy-load
          :src="chuli(item?.coverPathSmall)"
        />
        <div class="hotbox">
          <p class="tingtitle">{{ item.title }}</p>
          <p class="tingtime">
            <svg
              v-show="item.contentType == 1"
              t="1665577203435"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="23495"
              width="48"
              height="48"
            >
              <path
                d="M374.6 636.5c4.4 0 8.5-1.2 12.1-3.3l171.7-100c8-3.6 13.6-11.9 13.6-21.5 0-8.8-4.8-16.6-11.9-20.7l-167.8-97.8c-4.3-5-10.7-8.1-17.7-8.1-13.1 0-23.6 10.7-23.6 23.8v1.3l-0.3 0.2 0.4 199.8c-0.1 0.8-0.1 1.6-0.1 2.5 0 13.2 10.6 23.8 23.6 23.8z"
                fill="#a3a3ac"
                p-id="23496"
              ></path>

              <path
                d="M64.7 586.3a32.2 32.1 0 1 0 64.4 0 32.2 32.1 0 1 0-64.4 0Z"
                fill="#a3a3ac"
                p-id="23497"
              ></path>

              <path
                d="M960 398.3c0.1-1.6 0.2-3.2 0.2-4.8 0-35-28.5-63.3-63.6-63.3-11.7 0-22.7 3.2-32.2 8.7l-0.5-0.3-31.5 18.2v-64.7c-0.1-73.1-59.9-133-133.1-133H197.4c-73.1 0-133 59.8-133 133v165.8h0.2c0 17.7 14.4 32.1 32.2 32.1s32.2-14.4 32.2-32.1h0.2V287c0-35.2 28.8-64 64-64h510.2c35.2 0 64 28.8 64 64v448.9c0 35.2-28.8 64-64 64H193.3c-35.2 0-64-28.8-64-64v-21.4c0-17.7-14.4-32.1-32.2-32.1-17.8 0-32.2 14.4-32.2 32.1h-0.4v15.3c0 73.2 59.9 133 133 133h501.9c73.2 0 133-59.8 133-133v-64.1l33.1 19.1 0.1-0.1c9.2 5.1 19.8 8 31 8 35.1 0 63.6-28.4 63.6-63.3 0-1.6-0.1-3.2-0.2-4.8V398.3z m-63.6 205.1c-0.3 7.8-6.9 14.1-15 14.1-2.7 0-5.3-0.7-7.5-2l-41.5-23.7V430.1l40.9-23.2c2.3-1.5 5.1-2.3 8.1-2.3 8.3 0 15 6.6 15 14.6v184.2z"
                fill="#a3a3ac"
                p-id="23498"
              ></path>
            </svg>
            <svg
              v-show="item.contentType == 2"
              t="1666023621682"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="2569"
              width="200"
              height="200"
            >
              <path d="M0 0h1024v1024H0z" fill="#fff" p-id="2570"></path>
              <path
                d="M480 914.112a32 32 0 1 0 64 0h-64zM544 128a32 32 0 0 0-64 0h64zM288 754.112a32 32 0 0 0 64 0H288zM352 288a32 32 0 1 0-64 0h64zM96 626.112a32 32 0 1 0 64 0H96zM160 416a32 32 0 0 0-64 0h64z m512 338.112a32 32 0 1 0 64 0h-64zM736 288a32 32 0 0 0-64 0h64z m128 338.112a32 32 0 0 0 64 0h-64zM928 416a32 32 0 1 0-64 0h64zM544 914.112V128h-64v786.112h64z m-192-160V288H288v466.112h64z m-192-128V416H96v210.112h64z m576 128V288h-64v466.112h64z m192-128V416h-64v210.112h64z"
                fill="#a3a3ac"
                p-id="2571"
              ></path>
            </svg>
            <span class="count">{{ item.dataCount }}</span>
            <span class="date">{{ formatMsToDate(item.releaseAt) }}更新</span>
          </p>
        </div>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
var num = 0;
export default {
  data() {
    return {
      loading: false,
      finished: false,
      tingnav: null,
      tinglist: null,
      show: true,
      newtinglist: [],
      active: 2,
      fenlei: null,
      newfenlei: null,
      arr: null,
    };
  },
  methods: {
    clickhandle(n) {
      this.$router.push({
        path: "/ting",
        query: {
          id: n,
        },
      });
    },
    formatMsToDate(ms) {
      let d = new Date(ms);
      let fil = (n) => {
        n = Number(n);
        n = n < 10 ? `${n}`.padStart(2, "0") : n;
        return n;
      };
      return `${d.getFullYear()}-${fil(d.getMonth() + 1)}-${fil(d.getDate())} ${fil(
        d.getHours()
      )}:${fil(d.getMinutes())}`; // d.getTime
    },
    onLoad() {
      num++;
      // 异步更新数据
      this.axios
        .get(
          `https://apis.netstart.cn/ximalaya/ting/categorySubjectList/${this.$route.params.id}`,
          {
            params: {
              pageNum: num,
              pageSize: 10,
            },
          }
        )
        .then((res) => {
          // 加载状态结束
          (this.loading = false),
            (this.arr = res.data.data?.subjects),
            (this.newtinglist = new Set([...this.newtinglist, ...this.arr]));
          if (this.arr.length < 20 && num > 3) {
            this.finished = true;
          }
        })
        .catch(() => {
          this.loading = false;
          this.error = true;
        });
    },

    onClick(name, title) {
      let arr = this.tingnav;
      this.fenlei = arr.filter((o) => o.title == title);
      [this.newfenlei] = this.fenlei;
      if (this.$route.params.id !== name) {
        this.$router.push({ path: `/ting/${name}` });
      }
    },
    chuli: function (n) {
      if (n.startsWith("http")) {
        return n;
      } else if (!n.startsWith("http")) {
        return `${n}`;
      } else {
        return "1.png";
      }
    },
  },
  beforeDestroy() {
    console.log("内容销毁");
    num = 0;
  },
};
</script>
<style>
.van-cell__value {
  display: flex;
  align-items: center;
}
</style>
<style lang="less" scoped>
.newTingpage {
  margin-top: 100rem;
  position: relative;
  z-index: -1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hotbox {
  display: inline-block;
  width: 73.889%;
  height: 70px;
  vertical-align: middle;
  margin-left: 10px;
  .tingtitle {
    height: 44px;
    text-align: left;
    font-size: 16px;
    color: #40404c;
    letter-spacing: 0;
    white-space: normal;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    line-height: 22px;
    overflow: hidden;
  }
  .tingtime {
    margin-top: 10px;
    font-size: 12px;
    color: #a3a3ac;
    letter-spacing: 0.5px;
    line-height: 16px;
    display: flex;

    align-items: center;
    .icon {
      width: 15rem;
      height: 15rem;
    }
    .count {
      margin-left: 4.5px;
      margin-right: 14px;
    }
    .date {
    }
  }
}

.van-image {
  margin: 15px 0;
}
</style>
