<template>
  <div class="ting">
    <!-- <AppHeader /> -->
    <div class="headerBox">
      <div class="header">
        <!-- <div class="headerBox"> -->
        <div class="mark"></div>
        <div
          class="logo"
          @click="$router.path != '/tuijian' ? $router.push({ path: '/' }) : ''"
        ></div>
        <div class="search">
          <svg
            t="1664715880181"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="5518"
            width="48"
            height="48"
          >
            <path
              d="M851.710029 803.043632c67.147306-78.301344 107.720374-180.059935 107.720373-291.298435 0-247.27171-200.453405-447.724091-447.724091-447.724091S63.981197 264.473487 63.981197 511.745197s200.453405 447.724091 447.724091 447.724091c111.2385 0 212.997092-40.573067 291.298435-107.720374l97.632622 97.632621c13.449313 13.449313 35.255968 13.449313 48.706305 0 13.449313-13.449313 13.449313-35.255968 0-48.706305l-97.632621-97.631598z m-67.805292-19.10001l-22.499434 19.294439c-34.062793 29.209252-72.504314 52.006468-114.25623 67.759243-43.206034 16.301268-88.775906 24.566512-135.441739 24.566512-102.521978 0-198.907189-39.924291-271.40127-112.417349s-112.417349-168.879292-112.417349-271.40127 39.924291-198.907189 112.417349-271.40127S409.184333 127.926578 511.706311 127.926578c102.521978 0 198.907189 39.924291 271.40127 112.417349s112.417349 168.879292 112.417349 271.40127c0 46.666856-8.265244 92.236727-24.566512 135.441738-15.752776 41.75294-38.549991 80.193437-67.759243 114.256231l-19.294438 22.500456z"
              fill="#bfbfbf"
              p-id="5519"
            ></path>
          </svg>
          <input type="text" placeholder="搜索" @focus="showSearch()" />
        </div>
        <div class="openBtn">打开app</div>
        <!-- </div> -->
      </div>
    </div>
    <div class="warp" :class="{ downOpen: DownHeight }">
      <div class="tabs" ref="tabs">
        <ul ref="tabsUl">
          <li
            v-for="sub in tabs"
            :class="{ active: $route.params.id == sub?.name }"
            :key="sub?.cid"
            @click="
              toSub(sub?.name);
              $route.params.id !== sub?.name
                ? $router.replace({ path: `/ting/${sub.name}` })
                : '';
            "
          >
            {{ sub?.title }}
          </li>
        </ul>
      </div>
      <div class="warpIcon">
        <svg
          @click="toggleStatus(true)"
          t="1666084994794"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="4102"
          width="48"
          height="48"
        >
          <path
            d="M904.533333 311.466667c-17.066667-17.066667-42.666667-17.066667-59.733333 0L512 644.266667 179.2 311.466667c-17.066667-17.066667-42.666667-17.066667-59.733333 0-17.066667 17.066667-17.066667 42.666667 0 59.733333l362.666666 362.666667c8.533333 8.533333 19.2 12.8 29.866667 12.8s21.333333-4.266667 29.866667-12.8l362.666666-362.666667c17.066667-17.066667 17.066667-42.666667 0-59.733333z"
            p-id="4103"
            fill="#f66847"
          ></path>
        </svg>
      </div>
      <div class="open-up" :class="{ open: showOpen }">
        <div class="open-title">
          <div class="title">请选择分类</div>
          <div class="openIcon">
            <svg
              @click="toggleStatus(false)"
              t="1666084528705"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="3139"
              width="48"
              height="48"
            >
              <path
                d="M793.024 710.272a32 32 0 1 0 45.952-44.544l-310.304-320a32 32 0 0 0-46.4 0.48l-297.696 320a32 32 0 0 0 46.848 43.584l274.752-295.328 286.848 295.808z"
                p-id="3140"
                fill="#f66847"
              ></path>
            </svg>
          </div>
        </div>
        <ul class="open-container">
          <li v-for="sub in tabs" :key="sub?.cid">
            {{ sub?.title }}
          </li>
        </ul>
      </div>
    </div>

    <keep-alive>
      <NewTingPage />
    </keep-alive>

    <transition name="showPage">
      <searchPage
        class="searchPotion"
        v-show="showSearchPage"
        @Close-Page="closeSearch()"
        :showSearchPage="showSearchPage"
      />
    </transition>
  </div>
</template>

<script>
import SearchPage from "@/components/searchPage.vue";
import NewTingPage from "@/components/newTingPage.vue";
export default {
  components: {
    NewTingPage,

    SearchPage,
  },
  beforeRouteUpdate(to, from, next) {
    // console.log("现在是", to.params.id);
    // this.$forceUpdate();
    // console.log(this.$forceUpdate());
    setTimeout(() => {
      this.$router.go(0);
    });
    //更新数据
    this.axios
      .get(`https://apis.netstart.cn/ximalaya/ting/categorySubjectList/${to.params.id}`, {
        params: {
          pageNum: 1,
          pageSize: 10,
        },
      })
      .then((res) => (this.list = res.data.data.subjects));
    next();
  },

  data() {
    return {
      // 分类数据
      tabs: [],
      // 当前
      currentClassify: "remen",
      //
      showOpen: false,
      // 数据
      list: [],
      showSearchPage: false,
      oldScrollTop: 0,
      DownHeight: true,
    };
  },
  filters: {
    formatMsToDate(ms) {
      let d = new Date(ms);
      let fil = (n) => {
        n = Number(n);
        n = n < 10 ? `${n}`.padStart(2, "0") : n;
        return n;
      };
      return `${d.getFullYear()}-${fil(d.getMonth() + 1)}-${fil(d.getDate())}`;
    },
  },
  created() {
    this.axios.get("https://apis.netstart.cn/ximalaya/ting/categories").then((res) => {
      this.tabs = res.data.data.categories;
    });
    // https://apis.netstart.cn/ximalaya/ting/categorySubjectList/remen?pageNum=1&pageSize=10
    this.axios
      .get(
        `https://apis.netstart.cn/ximalaya/ting/categorySubjectList/${this.$route.params.id}`,
        {
          params: {
            pageNum: 1,
            pageSize: 20,
          },
        }
      )
      .then((res) => {
        this.list = res.data.data.subjects;
      });
  },
  beforeDestroy() {
    console.log("路由销毁前");
    document.querySelector("body").style.overflow = "initial";
  },
  methods: {
    scrolling() {
      try {
        let scrollTop =
          window.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop;

        let scrollStep = scrollTop - this.oldScrollTop;
        this.oldScrollTop = scrollTop;

        if (scrollStep < 0) {
          this.DownHeight = true;
          // this.$refs.list.style.height = "35rem";
        } else {
          this.DownHeight = false;
          // this.$refs.list.style.height = "0rem";
        }
        if (scrollTop == 0) {
          // console.log("到头了");
          this.DownHeight = true;
        }
      } catch (error) {
        error;
      }
    },
    toggleStatus: function (status) {
      if (status) {
        this.showOpen = status;
        document.querySelector("body").style.overflow = "hidden";
      } else {
        this.showOpen = status;
        document.querySelector("body").style.overflow = "initial";
      }
    },
    showSearch() {
      this.showSearchPage = true;
      document.querySelector("body").style.overflow = "hidden";
    },
    closeSearch() {
      this.showSearchPage = false;
      document.querySelector("body").style.overflow = "initial";
    },
    toSub(code) {
      this.showOpen = false;
      this.currentClassify = code;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.scrolling);
  },
};
</script>

<style lang="less" scoped>
