            width: 15rem;
            height: 15rem;
          }
        }
      }

      //展开的内容
      ul.open-container {
        transform: translateY(-100%);
        transition: all 0.3s;
        padding: 7.5rem;
        display: flex;
        flex-wrap: wrap;
        // box-shadow: 0 -1rem 1rem 0 rgba(100, 100, 100, 0.2);
        height: 345rem;
        overflow: auto;
        li {
          width: 27%;
          height: 32rem;
          font-size: 16rem;
          margin: 7.5rem;
          text-align: center;
          line-height: 32rem;
          flex-grow: 1;
          color: #40404c;
          background-color: #f3f4f5;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
  }
  ul.ting-container {
    li {
      padding-left: 15rem;
      display: flex;
      padding: 15rem 0 15rem 15rem;
      box-shadow: 0 1rem 1rem 0 rgba(100, 100, 100, 0.1);
      .title {
        padding-left: 10rem;

        p {
          height: 44rem;
          text-align: left;
          font-size: 16rem;
          color: #40404c;
          letter-spacing: 0;
          white-space: normal;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          line-height: 22rem;
          overflow: hidden;
        }
        .tags {
          margin-top: 10rem;
          font-size: 12rem;
          color: #a3a3ac;
